#skills {
    font-family: 'Roboto', sans-serif;
    height: 100vh;
    background-color: #C26868;
}

#skillsTop {
    height: 0;
    background-color: #C26868;
    position: relative;
    display: none;
}

#skillsBot {
    height: 100%;
    background-color: #C26868;
    position: relative;
}

#left {
    width: 20%;
    height: 100%;
    float: left;
}

#right {
    width: 80%;
    height: 100%;
    float: right;
}

.carousel {
    width: 200%;
    height: 35%;
}

.carousel .carousel-item {
    width: 150px !important;
    height: 150px !important;
    padding-right: 20px;
    padding-left: 20px;
}

.carousel .carousel-item img {
    -webkit-transform: rotateZ(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotateZ(-90deg);
}

#typedBox {
    width: 60%;
    height: 80%;
    background-color: black;
    -webkit-transform: translateX(5%) translateY(7.5%);
        -ms-transform: translateX(5%) translateY(7.5%);
            transform: translateX(5%) translateY(7.5%);
    border-radius: 10px;
}

#typedHeader {
    width: 100%;
    height: 10%;
    background-color: #6a6a6a;
    border-radius: 10px 10px 0 0;
    font-family: 'Ubuntu Mono', monospace;
    text-align: center;
    position: relative;
}

#typedHeader p {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-100%);
        -ms-transform: translate(-50%,-100%);
            transform: translate(-50%,-100%);
    font-size: 1.5em;
}

#typedBody {
    font-size: 1.2rem;
    width: 100%;
    height: 90%;
    border-radius: 10px 10px 0 0;
    color: white;
    font-family: 'Ubuntu Mono', monospace;
    padding: 10px 10px 0 10px;
}

#resume {
    position: absolute;
    bottom: 0;
}

.typed-cursor{
    opacity: 1;
    animation: typedjsBlink 0.7s infinite;
    -webkit-animation: typedjsBlink 0.7s infinite;
    animation: typedjsBlink 0.7s infinite;
}

#typedSpan {
    white-space: pre;
}

@media (min-width: 1500px) {
    .carousel {
        -webkit-transform: translateX(20%) translateY(-40%) rotateZ(90deg);
            -ms-transform: translateX(20%) translateY(-40%) rotate(90deg);
                transform: translateX(20%) translateY(-40%) rotateZ(90deg);
    }
}

@media (min-width: 1300px) and (max-width: 1499px) {
    .carousel {
        -webkit-transform: translateX(20%) translateY(-15%) rotateZ(90deg);
            -ms-transform: translateX(20%) translateY(-15%) rotate(90deg);
                transform: translateX(20%) translateY(-15%) rotateZ(90deg);
    }
}

@media (min-width: 1025px) and (max-width: 1299px) {
    .carousel {
        -webkit-transform: translateX(20%) translateY(10%) rotateZ(90deg);
            -ms-transform: translateX(20%) translateY(10%) rotate(90deg);
                transform: translateX(20%) translateY(10%) rotateZ(90deg);
    }
}

@media (min-width: 300px) and (max-width: 1024px) {
    #section {
        font-family: 'Ubuntu Mono', monospace;
        height: 100%;
        margin: 0;
        padding-top: 15px;
        font-size: 2rem;
        text-align: center;
    }

    #left {
        width: 100%;
        height: 20%;
    }

    #right {
        width: 100%;
        height: 80%;
        position: relative;
    }

    #skillsTop {
        display: block;
        height: 7%;
    }

    #skillsBot {
        height: 93%;
    }
    .carousel {
        width: 100%;
        height: 100%;
        -webkit-transform: translateX(0);
            -ms-transform: translateX(0);
                transform: translateX(0);
    }

    .carousel .carousel-item {
        width: 110px !important;
        height: 110px !important;
    }

    .carousel .carousel-item img {
        -webkit-transform: rotateZ(0deg);
            -ms-transform: rotate(0deg);
                transform: rotateZ(0deg);
        width: 110px !important;
        height: 110px !important;
    }

    #typedBox {
        width: 80%;
        height: 75%;
        position: absolute;
        left: 50%;
        top: 5%;
        -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
                transform: translateX(-50%);
    }


}

@media (min-width: 300px) and (max-width: 767px) {
    #left {
        height: 20%;
    }

    #right {
        height: 80%;
    }

    .carousel .carousel-item {
        width: 90px !important;
        height: 90px !important;
    }

    .carousel .carousel-item img {
        width: 90px !important;
        height: 90px !important;
    }
    #typedBox {
        width: 95%;
        height: 75%;
        top: 5%;
        position: absolute;
    }
}

@media (min-width: 300px) and (max-width: 900px) and (orientation: landscape) {
    #skillsTop {
        display: none;
    }

    #skillsBot {
        height: 100%;
    }

    #left {
        width: 20%;
        height: 100%;
        float: left;
    }

    #right {
        width: 80%;
        height: 100%;
    }

    .carousel {
        width: 200%;
        height: 35%;
        -webkit-transform: translateX(20%) translateY(-40%) rotateZ(90deg);
            -ms-transform: translateX(20%) translateY(-40%) rotate(90deg);
                transform: translateX(20%) translateY(-40%) rotateZ(90deg);
    }

    .carousel .carousel-item {
        width: 75px !important;
        height: 75px !important;
    }

    .carousel .carousel-item img {
        width: 75px !important;
        height: 75px !important;
        -webkit-transform: rotateZ(-90deg);
            -ms-transform: rotate(-90deg);
                transform: rotateZ(-90deg);
    }

    #typedHeader p {
        font-size: 1.3rem;
    }

    #typedBox {
        left:0;
        width: 70%;
        height: 75%;
        top: 5%;
        position: absolute;
        -webkit-transform: translateX(0);
            -ms-transform: translateX(0);
                transform: translateX(0);
    }
}

@media (min-width: 0px) and (max-width: 300px) {
    #skillsTop {
        display: none;
    }

    #skillsBot {
        display: none;
    }
}






