#nav {
    display: none;
}

#navHolder {
    position: absolute;
    width: 50%;
    height: 50%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.nav-button {
    width: 3%;
    height: 100%;
    background-color: grey;
    border-style: solid;
    border-width: 1px;
    border-radius: 3px;
    float: left;
}

.nav-item {
    width: 100%;
    height: 10%;
    margin-top: 10px;
    position: relative;
}

.navTitle {
    color: white;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    font-family: 'Roboto', sans-serif;
}

@media (min-width: 1025px) {
    #nav {
        position:fixed;
        width: 20%;
        height: 100vh;
        right: 0;
        z-index: 1;
        display: inline-block;
    }


}

@media (min-width: 300px) and (max-width: 900px) and (orientation: landscape) {
    #nav {
        position:fixed;
        width: 20%;
        height: 100vh;
        right: 0;
        z-index: 1;
        display: inline-block;
    }
}


